










import { Component, Vue } from 'vue-property-decorator';
import * as THREE from 'three';
import Camera from './Camera';
import Controller from './Controller.vue';
import HtmlControl, { positions } from './HtmlControl.vue';

const pi = Math.PI;
const mapLimit = 7;
let pos = [0, 1.5, -5];
let dir = 0;

@Component({
  components: {
    Controller,
    HtmlControl,
  },
})
export default class ThreeApp extends Vue {
  private renderer!: THREE.WebGLRenderer;

  private inner = { width: window.innerWidth, height: window.innerHeight };

  private camera!: THREE.PerspectiveCamera;

  private scene!: THREE.Scene;

  private state: { x: number, y: number, z: number, thetaZ: number, thetaXY: number } = {
    x: -7,
    y: 0,
    z: 1.5,
    thetaXY: 0,
    thetaZ: 0,
  };

  private onResize() {
    this.inner = { width: window.innerWidth, height: window.innerHeight };
    this.renderer.setSize(this.inner.width, this.inner.height);
    this.camera.aspect = window.innerWidth / window.innerHeight;
    this.camera.fov = 75;
    this.renderer.render(this.scene, this.camera);
  }

  private mounted() {
    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setSize(this.inner.width, this.inner.height);
    // this.renderer.setClearColor('#ffffff');
    this.$el.appendChild(this.renderer.domElement);

    this.camera = Camera();
    this.scene = new THREE.Scene();

    this.initObject();

    window.addEventListener('resize', this.onResize);
  }

  private beforeDestroy() {
    window.removeEventListener('resize', this.onResize);
  }

  private land!: THREE.Mesh;
  
  private sphere!: THREE.Mesh;

  private async initObject() {
    const map = new THREE.TextureLoader().load('running.jpg');
    setTimeout(() => {
      const geoLand = new THREE.PlaneGeometry(mapLimit*2, mapLimit*2);
      const matLand = new THREE.MeshPhongMaterial({ color: 0xffffff, map });
      this.land = new THREE.Mesh(geoLand, matLand);
      this.land.position.set(0, 0, 0);
      this.land.rotateX(-pi / 2);
      this.scene.add(this.land);

      const geoWall = new THREE.PlaneGeometry(mapLimit*2, mapLimit*2);
      const matWall1 = new THREE.MeshPhongMaterial({ color: 0xffffdd, side: THREE.DoubleSide });
      const wall = new THREE.Mesh(geoWall, matWall1);
      wall.position.set(0, 0, mapLimit);
      wall.rotation.z = pi / 2;
      this.scene.add(wall);

      const matWall2 = new THREE.MeshPhongMaterial({ color: 0xffddff, side: THREE.DoubleSide });
      const wall2 = new THREE.Mesh(geoWall, matWall2);
      wall2.position.set(0, 0, -mapLimit);
      wall2.rotation.z = pi / 2;
      this.scene.add(wall2);

      const matWall3 = new THREE.MeshPhongMaterial({ color: 0xddffff, side: THREE.DoubleSide });
      const wall3 = new THREE.Mesh(geoWall, matWall3);
      wall3.position.set(-mapLimit, 0, 0);
      wall3.rotation.y = pi / 2;
      this.scene.add(wall3);

      const matWall4 = new THREE.MeshPhongMaterial({ color: 0xfddfff, side: THREE.DoubleSide });
      const wall4 = new THREE.Mesh(geoWall, matWall4);
      wall4.position.set(mapLimit, 0, 0);
      wall4.rotation.y = pi / 2;
      this.scene.add(wall4);

      const sphereGeo = new THREE.SphereGeometry(0.4, 32, 16);
      const sphereMat = new THREE.MeshPhongMaterial({ color: 0xaaaaff });
      this.sphere = new THREE.Mesh(sphereGeo, sphereMat);
      this.sphere.position.set(0, 0, -5);
      this.scene.add(this.sphere);

      positions.forEach((el)=> {
        const wireGeo = new THREE.CylinderGeometry(1.5, 1.5, 1.5, 100);
        const wireMat = new THREE.MeshPhongMaterial({
          // wireframeLinewidth: 1,
          // wireframe: true,
          color: 0x87ceeb,
        });
        const wire = new THREE.Mesh(wireGeo, wireMat);
        wire.position.set(el.y, 0, el.x);
        this.scene.add(wire);

      })

      const light = new THREE.PointLight(0xddddff, 0.9);
      light.castShadow = true;
      light.position.set(0, mapLimit*2, 0);
      this.scene.add(light);

      this.renderer.render(this.scene, this.camera);
    }, 300);
  }

  private onMove(value: number[]): void {
    dir -= value[0] * (pi / 4000);
    const cos = Math.cos(dir);
    const sin = Math.sin(dir);

    let dx = - value[1] * sin * 0.002; // + value[0] * cos * 0.002;
    let dz = - value[1] * cos * 0.002; // + value[0] * sin * 0.002;
    
    pos = getPos(pos, dz, dx);

    this.sphere.position.set(
      pos[0],
      0,
      pos[2],
    );

    const camX = pos[2] - 2 * cos;
    const camY = pos[0] - 2 * sin;
    
    this.state = {
      ...this.state,
      x: camX,
      y: camY,
      thetaZ: dir,
    };

    this.camera.position.x = pos[0] - 2 * sin;
    this.camera.position.z = pos[2] - 2 * cos;

    this.camera.lookAt(
      pos[0] + sin * 4,
      pos[1],
      pos[2] + cos * 4,
    );
    this.renderer.render(this.scene, this.camera);
  }
}

function getPos(yzx: number[], dx: number, dy: number) {
  let x = yzx[2];
  let y = yzx[0];
  const newX = x + dx; 
  const newY = y + dy; 
  
  let result = [newY, yzx[1], newX];
  let limit = false
  const mapThres = 0.4;
  if (x + dx > mapLimit-mapThres) {
    result[2] = mapLimit - mapThres;
    limit = true;
  } else if (x + dx < -mapLimit+0.4) {
    result[2] = -(mapLimit - mapThres);
    limit = true;
  }

  if (y + dy > mapLimit-mapThres) {
    result[0] = mapLimit - mapThres;
    limit = true;
  } else if (y + dy < -mapLimit+mapThres) {
    result[0] = -(mapLimit - mapThres);
    limit = true;
  }
  if (limit){
    return result;
  }

  if (distancePow(x + dx, y) < 3.61) { 
    const v_b =[ y , - x]
    result[2] = x + v_b[0] * (dx * v_b[0] + dy*v_b[1]) / (v_b[0] * v_b[0] + v_b[1] * v_b[1]);
  } 

  if (distancePow(x, y + dx) < 3.61) { 
    const v_b =[ y , - x]
    result[0] = y + v_b[1] * (dx * v_b[0] + dy*v_b[1]) / (v_b[0] * v_b[0] + v_b[1] * v_b[1]);
  } 
  
  return result
}

function dist(x: number, y: number) {
  return Math.sqrt(x*x + y*y)
}

function distancePow(x: number, y: number) {
  return x*x + y*y;
}
